import React, {Component} from "react";
import {getPapers} from "../services/fakePaperService";
import { Button } from 'react-bootstrap';

class Research extends Component {
    state = {  
        papers: getPapers()        
    };

    componentWillUnmount() {
        this.setState({papers: getPapers()})
    }

    getCoauthorInfo = (coauthor,last) => {
        
        if (last) {
        return <React.Fragment key={coauthor._id}> 
            <span> and </span>
            <a href= {coauthor.link} target="_blank" key={coauthor._id}>
                {coauthor.name}, 
            </a> </React.Fragment>;
        } else {
        return <a href= {coauthor.link} target="_blank" key={coauthor._id}>{coauthor.name}, </a>;
        }
    }

    getOtherInfo = info => {
        

        return <> {info}</>;
        
    }    

    getPaperInfo = paper => {
        //const papers = [...this.state.papers];
        //const index = papers.indexOf(paper);
        const coauthors = paper.coauthors;
        
        if (coauthors) {
            return <React.Fragment>
                <span className = "second-word-formatting" >, with </span> 
                {
                coauthors.map((coauthor, i) => (
                    this.getCoauthorInfo(coauthor, (i+1===coauthors.length && coauthors.length > 1))                
                ))}
                
                {paper.status === 'published' && <span 
                    className = "second-word-formatting">
                        <span style={{textDecoration : "underline"}}><i>{paper.journal},</i></span> </span> 
                }
                <span className = "second-word-formatting"> <i>{paper.publishDate}</i> </span> 
            </React.Fragment>
        } else 
        return <span className = "second-word-formatting"><i> {paper.publishDate}</i> </span> ;
    };

    getMediaCoverage = paper => {
        //const papers = [...this.state.papers];
        //const index = papers.indexOf(paper);
        const media = paper.media;
        
        if (media) return (
        <li> 
            Media Coverage: {media.map((m,i) => (
                <React.Fragment key={m.media}>
                <a href = {m.link} target="_blank" key={m.media}>{m.media}</a> 
                {media.length!==i+1 && <span>, </span>}
                </React.Fragment>
            ))} 
        </li>
        );

        return null;
    };

    filterKeyWord = keyword => {
      const papers = getPapers();
      if (keyword !== 'all') {
        const selectedPapers = papers.filter(p => (p.keyWords.includes(keyword)));
        this.setState({papers:selectedPapers});
      } 
      else {this.setState({papers})};
      
    };

    getKeyWords = paper => {
        const keyWords = paper.keyWords;
        
        if (keyWords) return (
            <li> 
            {keyWords.map(k => (
                <React.Fragment key={k}>
                <Button 
                    onClick={() => this.filterKeyWord(k)} 
                    variant="outline-dark" 
                    key={k}
                >
                   <h6> #{k} </h6>
                </Button> {' '}
                </React.Fragment>
            ))} 
            </li>
        );
    };    

    render() { 
        const publications = this.state.papers.filter(p => p.status === 'published');
        const workingPapers = this.state.papers.filter(p => p.status === 'wp');
        
        return <React.Fragment>
            
            
            <div>
            <h2>Publications</h2>
            
            <ul key='pubs'>                
            {publications.map(paper => (
                <li key={paper._id}>
                    <a href = {paper.paperLink} target="_blank">{paper.title}</a>{this.getPaperInfo(paper)}
                    <ul>
                        {this.getMediaCoverage(paper)}
                        {this.getKeyWords(paper)}
                    </ul>
                </li>
            )
            )}
            </ul>
            </div>

            <div>
                <h2>Working Papers</h2>
            
            <ul key='wps'>
                
            {workingPapers.map(paper => (
                <li key = {paper._id}>
                    <a href = {paper.paperLink} target="_blank">{paper.title}</a>{this.getPaperInfo(paper)}
                    <ul key={paper._id}>
                    {paper.otherInfo !== undefined && <li>{paper.otherInfo}</li>}
                        {paper.conferences.length > 0 && 
                        <li key={paper._id}> {paper.conferences.map((c,j) => (
                            <span key={c}>{c}{j+1 !== paper.conferences.length && <React.Fragment>,</React.Fragment>} </span> ))} </li>                      
                        }
                            

                        
                        {this.getMediaCoverage(paper)}
                        {this.getKeyWords(paper)}
                    </ul>                      
                </li>
            )
            )}
            </ul>
            </div>

            <Button
                    onClick={() => this.filterKeyWord('all')} 
                    className="btn btn-dark m-2" 
                    key='all research'
                >
                    <h6>View All Papers</h6>
            </Button> 
            </React.Fragment>;
        
    }


}
 
 
export default Research;