import React from "react";
import { getResources } from "../services/fakeResourcesService";

const Resources = () => {
  const resources = getResources();

  return (
    <div className="container">
      <h2>Useful Datasets</h2>
      <ul>
        {resources.map((r) => (
          <li key={r._id}>
            <a key={r._id} href={r.link} target="_blank">
              {r.resource}
            </a>
            <ul key={r._id}>
              <li key={r._id}> {r.description}</li>
            </ul>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Resources;
